.rsvp-header {
	height: calc(100% - 84px);
	background-position: center 34%;
	padding: 0;
}

.rsvp-form {
	width: 75%;
  margin: 0 auto;
  text-align: center;
  padding: 30px 0;
}

#rsvp {
	margin-top: 20px;
	.rsvpParagraph {
		margin: 0;
		font-style: italic;
		padding-top: 15px;

		label {
			width: 100%;
			display: block;
			padding-bottom: 5px;
		}

		input[type=text] {
			width: 50%;
			border: none;
			border-bottom: 1px solid #ccc;
			border-radius: 0;
			text-align: center;
		}

		input[type=submit] {
			padding: 8px 16px;
			text-decoration: none;
			text-transform: uppercase;
			font-size: 14px;
			border: 1px solid $text_accent;
			letter-spacing: 3px;
			background: none;
			margin-top: 15px;
			&:hover {
				color: $text_light;
				background: $text_accent;
			}
			&:visited {
				color: $color__text-main;
			}
		}
	}
}

